import {Component, Input, OnInit} from '@angular/core';
import {Location} from '@angular/common';
import {Router} from '@angular/router';
import {UserInfo} from '../../user/user-info';
import {getCookie, removeCookie} from '../../storage/storage';
import {SocketService} from './socket.service';
import {StorageService} from '../../storage/storage.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  messageCount = '';
  @Input()
  public currUser: UserInfo;

  constructor(private router: Router, private socketService: SocketService,private storageService:StorageService,
              private location: Location) {
  }

  ngOnInit() {
    this.socketService.createObservableSocket('ws://47.95.219.65:85')
      .map(event => JSON.parse(event))
      .subscribe(
        event => this.messageCount = event.messageCount
  )
  }

  offline() {
    removeCookie('userId');
    this.router.navigateByUrl('/login');
  }

  lockScreen() {
    const url = this.location.path();
    this.router.navigate(['/lock-screen',url]);
  }

}



