import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';

import {AppComponent} from './app.component';
import {RegisterComponent} from './register/register.component';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {RegisterService} from './register/register.service';
import {LoginComponent} from './login/login.component';
import {RouterModule, Routes} from '@angular/router';
import {Page404Component} from './page404/page404.component';
import {UserInfoService} from './user/user-info.service';
import {StorageService} from './storage/storage.service';
import {LoginGuard} from './guard/login.guard';
import {LockScreenComponent } from './lock-screen/lock-screen.component';
import {HttpModule} from '@angular/http';
import {EmployeeModule} from './employee/employee.module';
import {SelectivePreloadingStrategy} from './preload/selective-preloading-strategy';

const routeInfo: Routes = [
  {path: '', redirectTo: '/login', pathMatch: 'full'},
  {path: 'register', component: RegisterComponent},
  {path: 'login', component: LoginComponent},
  {path: 'lock-screen/:url', component: LockScreenComponent},
  {
    path: 'employee',
    loadChildren:'./employee/employee.module#EmployeeModule',
    data: {preload: true}
  },
  {path: '**', component: Page404Component}
];

@NgModule({
  declarations: [
    AppComponent,
    RegisterComponent,
    LoginComponent,
    Page404Component,
    LockScreenComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    ReactiveFormsModule,
    EmployeeModule,
    RouterModule.forRoot(routeInfo,{preloadingStrategy: SelectivePreloadingStrategy})
  ],
  providers: [RegisterService, UserInfoService, StorageService, LoginGuard, SelectivePreloadingStrategy],
  bootstrap: [AppComponent]
})
export class AppModule {
}



