import {Component, OnInit} from '@angular/core';
import { Form} from '../../../user/user-info';
import {FormService} from '../form.service';
import {getCookie} from '../../../storage/storage';
import {isNumber} from 'util';

@Component({
  selector: 'app-form-list',
  templateUrl: './form-list.component.html',
  styleUrls: ['./form-list.component.css']
})
export class FormListComponent implements OnInit {
  userId;
  public formLists: Array<Form>;
  public formList: Array<Form>;
  public form = new Form('', '', '', '', '', '');
  pageArr: Array<number> = [];
  page = 1;
  loading = false;
  showDetail = false;

  constructor(private formService: FormService) {
  }

  ngOnInit() {
    this.userId = getCookie('userId');
    this.formService.getFormList(this.userId, (data) => {
      this.pageArr.length = Math.ceil(data.length / 10);
      this.formLists = data;
      this.getPageList(this.formLists);
      this.loading = true;
    });

  }

  getPageList(formLists: Array<Form>){
    this.formLists.reverse();
    if (this.page === this.pageArr.length) {
      this.formList = formLists.slice((this.page - 1) * 10, formLists.length)
    } else {
      this.formList = formLists.slice((this.page - 1) * 10, this.page * 10)
    }
  }

  changePage(value:any){
    if(isNumber(value)) {
      this.page = value;
      this.getPageList(this.formLists);
      return;
    }
    if (value === 'prev' && this.page > 1) {
      this.page -= 1;
      this.getPageList(this.formLists);
      return;
    } else if (value === 'next' && this.page!=this.pageArr.length) {
      this.page += 1;
      this.getPageList(this.formLists);
    }
  }

  show(form: Form) {
    this.showDetail = true;
    this.form = form;
  }

}



