import {Injectable} from '@angular/core';
import {Http, Headers, RequestOptions} from '@angular/http';
import {Form} from '../../user/user-info';

@Injectable()
export class FormService {

  constructor(public http: Http) {
  }

  getFormList(userId, callback) {
    this.http.get('/api/formList/' + userId).subscribe(res => {
      callback(res.json());
    });
  }

  getFormNumber(formDate, callback) {
    this.http.get('/api/formApply/' + formDate).subscribe(formNumber => {
      callback(formNumber.json())
    })
  }

  saveForm(form: Form,callback) {
    let body = JSON.stringify(form);
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({headers: headers});
    this.http.post('/api/addForm', body, options).subscribe(res=>{
      callback(res.json())
    })
  }
}



