import {Component, OnInit} from '@angular/core';
import {UserInfo} from '../../user/user-info';
import {UserInfoService} from '../../user/user-info.service';
import {ActivatedRoute} from '@angular/router';
import {RegisterService} from '../../register/register.service';

@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.css']
})
export class UserListComponent implements OnInit {

  public isList;
  public departments = ['研发部', '资讯部', '人事部', '设计部', '工业工程部', '业务部'];

  public curDep = '研发部';
  public users: Array<UserInfo> = [];
  userId: string;

  constructor(private userInfoService: UserInfoService, private routeInfo: ActivatedRoute, private registerService: RegisterService) {
  }

  ngOnInit() {
    this.isList = true;
    this.userId = this.routeInfo.snapshot.queryParams['id'];
    if (this.userId) {
      this.registerService.returnUserInfo(function () {
        this.userInfoService.getUsersByDep(this.curDep, function (data) {
          this.users = data;
        }.bind(this));
      }.bind(this))
    }
  }

  update() {
    this.userInfoService.getUsersByDep(this.curDep, function (data) {
      this.users = data;
    }.bind(this));
  }
}




