import {Component} from '@angular/core';
import {UserInfoService} from './user/user-info.service';
import {getCookie} from './storage/storage';
import {StorageService} from './storage/storage.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  private initStore = true;

  constructor(private userInfoService: UserInfoService, private storageService: StorageService) {
    if (this.initStore) {
      if(getCookie('userId')){
        storageService.openDataBase([],function () {
          return;
        });
      }else{
        userInfoService.buildStorage();
      }
      this.initStore = false;
    }
  }
}



