import {Injectable} from '@angular/core';
import {AboutMe, EmployeeInfo, UserInfo} from '../user/user-info';
import {UserInfoService} from '../user/user-info.service';
import {StorageService} from '../storage/storage.service';
import {Router} from '@angular/router';
import {getCookie, setCookie} from '../storage/storage';

@Injectable()
export class RegisterService {

  public registerInfo: UserInfo = {id: '', dep: '', name: '', tel: '', email: '', password: '', photo: ''};
  public aboutMe: AboutMe;

  constructor(private storageService: StorageService, private router: Router) {
  }

  getRegisterInfo(id: string, password: string, callback): any {
    let result = [];
    this.storageService.get_data('employeesInfo', ['key', '', id], function (employee) {
      if (!employee) {
        result = [false, '员工不存在！'];
        callback(result);
      } else {
        this.storageService.get_data('usersInfo', ['key', '', id], function (user) {
          result = user ? [false, '用户已存在！'] : [employee, '注册成功！'];
          callback(result);
          if (result[0]) {
            setCookie('userId', result[0].id, 7);
            this.registerInfo = {
              id: result[0].id,
              name: result[0].name,
              dep: result[0].dep,
              tel: '未填写',
              email: '未填写',
              password: password,
              photo: 'assets/img/userimg.png'
            };
            this.storageService.add_data('usersInfo', [this.registerInfo]);
            this.aboutMe = {
              id: id,
              edu: '未填写',
              loc: '未填写',
              skill: '未填写',
              motto: '未填写'
            };
            this.storageService.add_data('aboutMe', [this.aboutMe]);
          }
        }.bind(this));
      }
    }.bind(this));
  }

  getLoginInfo(id: string, password: string, callback) {
    let result = [];
    this.storageService.get_data('usersInfo', ['key', '', id], function (user) {
      if (user && user.password === password) {
        this.registerInfo = user;
        result = [true, '登录成功！'];
        setCookie('userId', user.id, 7);
        callback(result);
      } else {
        result = [false, '用户不存在或密码错误！'];
        callback(result);
      }
    }.bind(this));
  }

  returnUserInfo(callback) {
    if (this.registerInfo.id != '') {
      callback(this.registerInfo);
    }
    if (getCookie('userId')) {
      const id = getCookie('userId');
      this.storageService.openDataBase([],function () {
        this.storageService.get_data('usersInfo', ['key', '', id], function (user) {
          this.registerInfo = user;
          callback(this.registerInfo);
        }.bind(this))
      }.bind(this));
    } else {
      this.router.navigateByUrl('/login');
    }
  }
}





