import {Component, DoCheck, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {checkValidator} from '../validators/Validators';
import {RegisterService} from '../register/register.service';
import {Router} from '@angular/router';

declare var $: any;

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit, DoCheck {

  public loginForm: FormGroup;
  public warning: string;

  // 存储错误信息
  formErrors = {
    'id': '',
    'password': ''
  };
  // 错误对应的提示
  validationMessages = {
    'id': {
      'required': '请输入工号'
    },
    'password': {
      'required': '请输入密码'
    }
  };

  constructor(public fb: FormBuilder, private registerService: RegisterService, private router: Router) {
  }

  ngOnInit() {
    this.loginForm = this.fb.group({
      id: ['', Validators.required],
      password: ['', Validators.required],
      rememberState: [true]
    });
  }

  ngDoCheck(): any {
    for (const field in this.formErrors) {
      this.formErrors[field] = '';
      // 取到表单字段
      const control = this.loginForm.get(field);
      // 表单字段已修改或无效
      if (control && control.touched && control.invalid) {
        // 取出对应字段可能的错误信息
        const messages = this.validationMessages[field];
        // 从errors里取出错误类型，再拼上该错误对应的信息
        for (const key in control.errors) {
          this.formErrors[field] += messages[key] + '';
        }
      }
    }
  }

  login() {
    const router = this.router;
    const id = this.loginForm.get('id').value;
    const password = this.loginForm.get('password').value;
    if (this.loginForm.valid) {
      this.registerService.getLoginInfo(id, password, function (data) {
        this.warning = data[1];
        $('.alert').fadeIn().delay(200).fadeOut();
        if (data[0]) {
          setTimeout(function () {
            router.navigate(['/employee/home']);
          }, 500);
        }
      }.bind(this));
    } else {
      this.warning = '请输入工号和密码！';
      $('.alert').fadeIn().delay(200).fadeOut();
    }
  }

  nav() {
    this.router.navigateByUrl('/register');
  }

}



