import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {RegisterService} from '../register/register.service';
import {UserInfo} from '../user/user-info';
import {ActivatedRoute, Router} from '@angular/router';
import {removeCookie} from '../storage/storage';

@Component({
  selector: 'app-lock-screen',
  templateUrl: './lock-screen.component.html',
  styleUrls: ['./lock-screen.component.css']
})
export class LockScreenComponent implements OnInit {
  public lockForm: FormGroup;
  public currUser = new UserInfo('','','','','','','');
  error = false;
  url;

  constructor(public fb: FormBuilder, private registerService: RegisterService, private router: Router,
              private routeInfo: ActivatedRoute) {
    this.lockForm = fb.group({
      password: ['', Validators.required]
    });
  }

  ngOnInit() {
    this.registerService.returnUserInfo(function (user) {
      this.currUser = user;
    }.bind(this));
    this.url = this.routeInfo.snapshot.params['url'];
  }

  getIn() {
    const password = this.lockForm.get('password').value;
    if (password === this.currUser.password) {
      this.error = false;
      this.router.navigateByUrl(this.url);
    }else{
      this.error = true;
    }
  }

  login(){
    removeCookie('userId');
    this.router.navigateByUrl('/login');
  }

}



